<template>
    <LoadingElement v-if="isLoading" />
    <v-card class="itemCard">
        <v-row class="addItemRow" >
            <v-col cols="12" class="coverColumn">
                <v-img class="ItemsImages" height="240" :src="item.photoURL[0]" cover></v-img>
            </v-col>

            <v-col cols="12">
                <div class="cardHeader">
                    <p class="cardTitle" :title="item.Name">
                        <v-icon v-if="isGame" icon="mdi-pac-man" class="mr-1"></v-icon>
                        <v-icon v-else-if="isAccessory" icon="mdi-video-input-scart" class="mr-1"></v-icon>
                        <v-icon v-else icon="mdi-nintendo-game-boy" class="mr-1"></v-icon>
                    {{ item.Name }}
                    </p>
                </div>
                <v-divider class="my-3" thickness="3"></v-divider>
                <div class="productInfos">
                    <span v-if="isItem || isAccessory"><v-icon icon="mdi-alphabetical" class="mr-1"></v-icon>Brand : {{ item.Brand ? item.Brand : "Unavailable" }}</span>

                    <span v-if="isItem"><v-icon icon="mdi-gamepad-variant" class="mr-1"></v-icon>Model : {{ item.Model ? item.Model : "Unavailable" }}</span>

                    <span><v-icon icon="mdi-nintendo-switch" class="mr-1"></v-icon>Console : <span>{{ item.Console }}</span></span>

                    <span><v-icon icon="mdi-dropbox" class="mr-1"></v-icon>With Box : <span :class="item.OriginalBox ? 'greenBox' : 'redBox'">{{ item.OriginalBox ? 'Yes' : 'No' }}</span></span>

                    <span><v-icon icon="mdi-image-broken-variant" class="mr-1"></v-icon>Condition : {{ item.State }}</span>

                    <span v-if="isGame || isItem"><v-icon icon="mdi-currency-usd" class="mr-1"></v-icon>Price : <span class="priceBox">{{ item.PurchasePrice ? item.PurchasePrice : "Unavailable" }} €</span></span>

                    <span class="mt-2"><v-icon icon="mdi-tag-outline" class="mr-1"></v-icon>Tags : 
                        <span v-for="tag in item.Tags" :key="tag" class="mr-1">
                            <v-chip color="white" text-color="white" small>{{ tag }}</v-chip>
                        </span>
                        <span v-if="!(item.Tags) || item.Tags.length === 0">
                            <v-icon icon="mdi-cancel" class="mr-1"></v-icon>No tags
                        </span>
                    </span>
                </div>
                <v-card-actions class="flex justify-end">
                    <v-btn variant="outlined" class="editBtn" size="small" @click="editFunction">
                        Edit
                    </v-btn>
                    <v-btn variant="outlined" class="deleteBtn" @click="visible(true)" size="small">
                        Delete
                    </v-btn>
                </v-card-actions>
            </v-col>
        </v-row>
    </v-card>

    <div>
        <div class="mb-4">
            <v-col cols="auto">
                <v-dialog
                    transition="dialog-top-transition" class="modalDialog" v-model="showDeleteDialog">
                    <template v-slot:default="{ isActive }">
                        <v-card>
                            <form>
                            <v-toolbar
                                color="red"
                                class="text-center"
                                title="Would you like to delete this item ?"
                            ></v-toolbar>
                            <v-card-text class="modalText">
                                <p>Are you sure you want to delete {{ this.item.Name }} ? This action is irreversible.</p>
                            </v-card-text>
                            <v-card-actions class="justify-end">
                                <v-btn
                                    variant="text"
                                    @click="isActive.value = false"
                                >Cancel</v-btn>
                                <v-btn
                                    variant="elevated"
                                    color="red"
                                    @click="deleteFunction"
                                >Delete</v-btn>
                            </v-card-actions>
                            </form>
                        </v-card>
                    </template>
                </v-dialog>
            </v-col>
        </div>
    </div>
</template>

<script>
    import { deleteItem } from '../assets/js/gameSearchJs'; 
    import LoadingElement from '@/components/LoadingElement.vue';

    export default {
        name: 'DisplayCard',
        components: { LoadingElement },
        props: {
            item: Object,
            isItem: Boolean,
            isAccessory: Boolean,
            isGame: Boolean,
            page: String,
            userId: String
        },
        data() {
            return {
                isLoading: false,
                showDeleteDialog: false,
                area: ""   
            };
        },
        mounted() {
            if(this.isGame){
                this.area = "Games";
            } else if(this.isAccessory){
                this.area = "Accessories";
            } else {
                this.area = "Items";
            }
        },
        methods: {
            visible(val) {
                this.showDeleteDialog = val;
            },
            async deleteFunction() {
                this.isLoading = true;
                try {
                    await deleteItem(this.userId, this.item, this.area);
                    await this.$router.push({ path: '/' + this.page });
                    this.$router.go(0);
                } catch (error) {
                    console.error("Error deleting item: ", error);
                }
            },
            async editFunction() {
                this.isLoading = true;
                try {
                    const query = {};
                    query.userId = this.userId;
                    query.itemId = this.item.item_id;

                    this.$router.push({
                        path: `/add${this.area.toLowerCase()}`,
                        query: query,
                    }).then(() => {
                        this.$router.go(0);
                    });
                } catch (error) {
                    console.error("Error deleting item: ", error);
                }
            },
        }
    };
</script>

